html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100Vh;
  background-color: #212731;
  font-family: "Manrope", "sans-serif";
  font: 800;
  color: #cee3e9;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

header {
  position: absolute;
  left: -99999999px;
  top: -99999999px;
}

main {
  align-items: center;
  justify-content: center;
}

footer {
  text-align: center;
  position: absolute;
  bottom: 2rem;
  color: #52ffa8;
  font-size: 0.5rem;
}

.advice-container {
  position: relative;
  background-color: #323a49;
  width: auto;
  max-width: 900px;
  padding: 20px 30px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.25rem;
}

#advice-number {
  color: #52ffa8;
  text-transform: uppercase;
  letter-spacing: 4px;
}

.advice-text {
  font-size: 1.75rem;
  line-height: 1.3;
}

.long {
  font-size: 1.5rem;
}

.decoration {
  margin: 1.25rem 0 2.5rem 0;
  padding: 0px 2rem;
  width: 100%;
  height: 1.3em;
}

.dice-container {
  cursor: pointer;
  position: absolute;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: -35px;
  height: 4rem;
  width: 4rem;
  border: 0;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background-color: #52ffa8;
}
.dice-container svg {
  width: 1.5rem;
  height: 1.5rem;
}

.dice-container:hover {
  box-shadow: 0px 0px 60px 5px #52ffa9;
}

#mobile {
  display: none;
}

@media screen and (max-width: 500px) {
  main {
    padding: 0 1.25rem;
  }

  footer {
    font-size: 0.5rem;
  }

  .advice-container {
    padding: 20px;
    gap: 1rem;
    width: 325px;
  }

  .dice-container {
    width: 3rem;
    height: 3rem;
  }

  #advice-number {
    font-size: 0.75rem;
    letter-spacing: 2px;
  }

  #advice-text {
    line-height: 1.3em;
  }

  .advice-text {
    font-size: 1.5rem;
  }

  .long {
    font-size: 1.5rem;
  }

  #mobile {
    display: block;
  }

  #desktop {
    display: none;
  }
}